export default {
	data() {
		return {
			
		};
	},
	computed:{
		height(){
			const {platform,statusBarHeight} = uni.getSystemInfoSync()
			let height = statusBarHeight +4 //ios 24px
			if (platform.toLowerCase() == "android" ){
				height +=4 //android 28px
			}
			// 胶囊高度 32px 下边框6px height 状态栏高度
			return height+ 38 + "px"
		},
		marginTop(){
			const {platform,statusBarHeight} = uni.getSystemInfoSync()
			let height = statusBarHeight +4
			if (platform.toLowerCase() == "android" ){
				height +=4
			}
			return height + "px"				
		}
	},
	updated() {
		console.log(this.config)
	},
	mounted() {
		console.log(this.config)
		
	},
	props:{
		config:{
			type:Object,
			default(){
				return {
					title:"",
					bgcolor:"",
					type:2,
					linear:false,
					transparent:false,
					fontcolor:"#000",
					menuIcon:"",
					menuText:""
				}
			}
		},
		scrollTop:{
			type:Number,
			default:0
		},
		scrollMaxHeight:{
			type:Number,
			default:288
		}
	},
	methods:{
		back_(){
			//使用 uni.navigateBack 必须要使用 uni.navigateTo来跳转页面
			// 注意：调用 navigateTo 跳转时，调用该方法的页面会被加入堆栈，而 redirectTo 方法则不会。
			uni.navigateBack({
				delta:1
			});
		},
		home_(){
			// uni.switchTab({
			// 	url:"/pages/index/index"
			// })
			//自己更改，返回首页面
			uni.redirectTo({
				url:"../../pages/index/index"
			})
		},
		conduct_(){
			this.$emit("customConduct")
		}
	}
}
